* {
  font-family: "Montserrat";
  touch-action: manipulation;
}
body {
  background: white;
  overflow-x: hidden;
}
ol {
  list-style: none;
}
li {
  margin-top: 20px;
}
div.page {
  width: 20px;
  height: 20px;
  border-radius: 10px 50%;
  background: white;
  opacity: 0.5;
}
div.page[data-enabled="true"]:hover {
  opacity: 0.9;
  cursor: pointer;
}
div.page.current {
  opacity: 0.9;
  cursor: auto;
}
div.slide {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
}
div.slide.next {
  z-index: -2;
}
