.background {
  width: 100vw;
  height: 100vh;
  overflow: auto;
  display: block;
  background: url(media/1630412-blur.jpg) #edb7d9 no-repeat center/120%;
  background-size: cover;
}
.background.openning {
  animation-duration: 1s;
  animation-name: start-slide;
}
@keyframes start-slide {
  from {
    transform: scale(1.2);
  }
  to {
    transform: scale(1);
  }
}
.content {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: relative;
  top: 1em;
}
.content.openning {
  animation-name: fade;
  animation-duration: 2s;
}
.background.closing .content {
  display: none;
}
.description-flex-box {
  display: flex;
  flex-direction: column;
  min-width: 80vw;
  gap: 20px;
  height: 90vh;
  align-items: center;
  margin: auto;
}
.text {
  font-size: 1.1em;
}
button.next {
  color: #c2c2c2;
  font-size: 150%;
  background-color: rgba(0, 0, 0, 0.6);
  border: 5px rgba(209, 209, 209, 0.8) solid;
  padding: 10px 50px;
}
button.next:hover {
  background-color: rgba(0, 0, 0, 0.9);
  border: 5px rgba(240, 240, 240, 1) solid;
  color: white;
  cursor: pointer;
}
@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.heading {
  font-size: 2.1em;
  margin-bottom: 0;
}
.background.closing {
  animation-name: goOut;
  animation-duration: 4s;
  font-size: 21px;
}
.background.closing.right {
  animation-name: goOutRight;
  animation-duration: 3s;
  font-size: 21px;
}
@keyframes goOut {
  40% {
    margin-top: 50%;
    margin-left: -80vw;
    width: 10vw;
    height: 10vh;
    font-size: 10px;
  }
}
@keyframes goOutRight {
  50% {
    margin-top: 10%;
    margin-left: 140vw;
    width: 10vw;
    height: 10vh;
    font-size: 10px;
  }
}
div.iphone {
  background: url(media/iphone.png) no-repeat;
  width: 317px;
  height: 680px;
  padding: 30px 25px 0px 30px;
  background-size: cover;
  margin: auto;
}
div.shop-app {
  position: relative;
  background: black;
  overflow: none;
  top: 38px;
  height: 555px;
  width: 300px;
}
.explanators {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 80vh;
}
.explanation {
  text-shadow: 10px 2px 15px white;
}
.explanation:last-child {
  text-shadow: 0px 0px 20px white;
}
.explanation:hover,
.explanation:focus {
  background: #de76b4;
  padding: 10px 20px;
  border-radius: 10px;
  color: black;
  text-shadow: none;
}
.frame {
  border: none;
  background: black;
}
.code-link {
  font-weight: 500;
  color: white;
  text-shadow: 0px 2px 15px white;
}
div.link {
  margin-top: 20px;
  color: white;
}

.expo {
  overflow: hidden;
  background: #212121;
  border: 1px solid var(--color-border);
  border-radius: 4px;
  height: 505px;
  width: 100%;
}
